import React, { useCallback } from "react";

function App() {
  const handleUpload = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {},
    []
  );

  return (
    <>
      <div>0.02</div>
      <input
        onChange={handleUpload}
        type="file"
        name="image"
        accept=".png, .jpg, .jpeg"
      />
    </>
  );
}

export default App;
